import styles from "./ProfileInfo.module.css";
import Preloader from "../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from "../../assets/images/userPhoto.jpg";

const ProfileInfo = ({
  profile,
  status,
  updateUserStatus,
  isOwner,
  savePhoto,
}) => {
  if (!profile) {
    return <Preloader />;
  }

  const onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
      savePhoto(e.target.files[0]);
    }
  };

  return (
    <div>
      <div className={styles.description}>
        <img
          className={styles.userAvatar}
          src={profile.photos.large || userPhoto}
          alt="userProfilePhoto"
        />
        {isOwner && <input type={"file"} onChange={onMainPhotoSelected} />}
        <ProfileStatusWithHooks
          status={status}
          updateUserStatus={updateUserStatus}
        />
        <div className={styles.fullName}>{profile.fullName}</div>
        <div className={styles.aboutMe}>{profile.aboutMe}</div>
        <div className={styles.contacts}>
          {Object.entries(profile.contacts).map(([key, value]) => (
            <div key={key} className={styles.contactItem}>
              <span className={styles.contactKey}>{key}:</span>{" "}
              {value ? (
                <span className={styles.contactValue}>{value}</span>
              ) : (
                <span className={styles.nullValue}> </span>
              )}
            </div>
          ))}
        </div>
        <div className={styles.jobStatus}>
           :{" "}
          {profile.lookingForAJob ? (
            <span className={styles.positive}></span>
          ) : (
            <span className={styles.negative}></span>
          )}
          <div className={styles.jobDesc}>
            {profile.lookingForAJobDescription}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileInfo;
